import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'venue-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  @Input() title: string = '';

  @Input('title-align') titleAlign: 'left' | 'center' | 'right' = 'left';

  @Input('color') color: 'basic' | 'primary' | 'accent' | 'warn' = 'basic';

  expanded = false;

  constructor() {}

  ngOnInit(): void {}

  toggle() {
    this.expanded = !this.expanded;
  }
  toggleNav() {
    var nav = document.getElementsByTagName('nav')[0];
    if (nav!.clientHeight) {
      nav!.style.height = '0';
    } else {
      // Round to the nearest $grid-unit, because sometimes browsers
      // add extra pixels to this.
      nav!.style.height = Math.round(nav!.scrollHeight / 20) * 20 + 'px';
    }
  }
  onResize() {
    var nav = document.getElementsByTagName('nav')[0];
    nav!.style.height = '0';
  }
}
